import { HttpClient, HttpParams } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'task-mamager-app';

  SEARCH_API = "/tasks"

  constructor(private http: HttpClient) {}
  ngOnInit(){
  
  }

  taskList: Task[] = [];

  optionArray: string[] = ["All", "Completed", "Pending"]
  searchForm = new FormGroup({
    taskname: new FormControl(''),
    status: new FormControl('')
  });

  todoForm = new FormGroup({
    taskname: new FormControl('', Validators.required),
    description: new FormControl('')
  });

  onSearch(){
    console.log("From search button")
    let params = new HttpParams()
    if (this.searchForm.get('taskname').value) {
      params = params.set('name', this.searchForm.get('taskname').value)
    }
    if (this.searchForm.get('status').value) {
      params = params.set('status', this.searchForm.get('status').value)
    }

    this.http.get(this.SEARCH_API, {params: params}).subscribe((data:any) => {
      console.log('From search api: ', data)
      for (let i = 0; i < data.length; i++){
        let tmp: Task;
        tmp.taskname = data[i].taskname
        tmp.taskId = data[i].taskId
        tmp.status = data[i].status
        tmp.description = data[i].description
        this.taskList.push(tmp)
      }
    })
  }

  onSubmit(){
    console.log("From submit button")
  }

}

export class Task {
  taskname: String
  description: String
  status: Boolean
  taskId: String
}
